/** @jsx jsx */
import { jsx } from "theme-ui"
import React from "react";

function debounce(f1, cb) {
  let tim = null
  return function() {
    f1();
    clearTimeout(tim);
    tim = setTimeout(function() {
      console.log("false");
      cb.apply(this, arguments);
    }, 500);
  }
}

export function Debounce() {
  const input = React.useRef()
  const [isTyping, setIsTyping] = React.useState(false);

  React.useEffect(() => {
    const debounceFun = debounce(() => setIsTyping(true),() => setIsTyping(false));
    input.current.addEventListener('input', debounceFun);
    return (() => {
      input.current.removeEventListener('input', debounceFun);
    })
  }, [])

  return (
    <React.Fragment>
      <div>
        <span>debounce:&nbsp;</span>
        <input type="text" ref={input} />
        <span sx={{
          display: `inline-block`,
          transform: isTyping ? `translateY(0)` : `translateY(-100%)`,
          opacity: isTyping ? `1` : `0`,
          transition: `transform 400ms ease-in-out, opacity 400ms ease-in-out`,
        }}>debounce</span>
      </div>
    </React.Fragment>
  )
}
